<template>
  <ec-container height="100%" color-code="001" class="info" title="基地单元衔接清理" show-title>
    <ec-container-body>
      <ec-panel :auto="false">
        <ec-panel-toolbar>
          <ec-query-panel :defaultFold="true" @query="onQuery" @reset="onReset">
            <ec-query-item label="连续年度">
              <a-input-number v-model.trim="query.seriesYear" placeholder="请输入连续年度" />
            </ec-query-item>
            <ec-query-item label="调拨比例">
              <a-input-number v-model.trim="query.asPercentTemp" placeholder="请输入调拨比例" />
            </ec-query-item>
            <ec-query-item label="集中交易年度">
              <a-input-number
                disabled
                v-model.trim="query.busiYear"
                placeholder="请输入集中交易年度"
              />
            </ec-query-item>
            <template #inner>
              <ec-query-item label="商业单位">
                <select-picker
                  title="选择商业单位"
                  :lazy="false"
                  :search="false"
                  :fetchData="onFetchBusinessOrgList"
                  v-model="query.businesOrgCode"
                />
              </ec-query-item>
              <ec-query-item label="工业单位">
                <select-picker
                  title="选择工业单位"
                  :lazy="false"
                  :search="false"
                  :fetchData="onFetchIndustryOrgList"
                  v-model="query.industryOrgCode"
                />
              </ec-query-item>
              <ec-query-item label="烟叶类型">
                <ec-select placeholder="请选择烟叶类型" v-model="query.leafTypeCode">
                  <a-select-option v-for="item in leafTypeList" :key="item.leafTypeCode">
                    {{ item.leafTypeName }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>
              <ec-query-item label="基地单元名称">
                <a-input v-model.trim="query.baseCellName" placeholder="请输入基地单元名称" />
              </ec-query-item>
              <ec-query-item label="基地单元编码">
                <a-input v-model.trim="query.baseCellCode" placeholder="请输入基地单元编码" />
              </ec-query-item>
            </template>
          </ec-query-panel>
          <ec-divider />
        </ec-panel-toolbar>
        <ec-panel-body>
          <ec-v-layout :auto="false">
            <!-- 表格工具栏 -->
            <ec-v-item height="auto">
              <ec-panel-toolbar>
                <ec-panel-item-left>
                  <a-button type="primary" @click="onClear">清理</a-button>
                  <a-button type="primary" ghost @click="handleUpdate">已清理单元</a-button>
                  <a-button type="primary" ghost @click="onCalculate">计算上一年调拨比例</a-button>
                </ec-panel-item-left>
                <ec-panel-item-right>
                  <div class="unit-box">
                    <span>集中交易年度：{{ busiYear }}年</span>
                    <div class="select">
                      重量单位：
                      <ec-select showSearch v-model.trim="weightUnit">
                        <a-select-option v-for="item in weightUnitList" :key="item.enumKey">
                          {{ item.name }}
                        </a-select-option>
                      </ec-select>
                    </div>
                    <div class="select">
                      面积单位：
                      <ec-select showSearch v-model.trim="areaUnit">
                        <a-select-option v-for="item in areaUnitList" :key="item.enumKey">
                          {{ item.name }}
                        </a-select-option>
                      </ec-select>
                    </div>
                  </div>
                </ec-panel-item-right>
              </ec-panel-toolbar>
            </ec-v-item>
            <!-- 列表 -->
            <ec-v-item>
              <ec-table
                ref="tableRef"
                :loading="loading"
                show-overflow
                height="100%"
                :auto="false"
                @page-action="onPageAction"
                :pagination="pagination"
                :data="tableData"
                @checkbox-all="onSelAllTableData"
                @checkbox-change="onSelTableData"
              >
                <ec-table-col type="checkbox" width="60" align="center"></ec-table-col>
                <ec-table-col type="seq" title="序号" width="50" align="center" />
                <ec-table-col field="busiYear" title="创建年度" align="center" min-width="100" />
                <ec-table-col
                  field="linkBeginYear"
                  title="衔接年度"
                  align="center"
                  min-width="100"
                />
                <ec-table-col field="provOrgName" title="省公司" align="center" min-width="150" />
                <ec-table-col field="cityOrgName" title="市公司" align="center" min-width="150" />
                <ec-table-col field="provDivisionName" title="省" align="center" min-width="150" />
                <ec-table-col
                  field="cityDivisionName"
                  title="地(州、市)"
                  align="center"
                  min-width="150"
                />
                <ec-table-col
                  field="counDivisionName"
                  title="县(区、市)"
                  align="center"
                  min-width="150"
                />
                <ec-table-col
                  field="baseCellCode"
                  title="基地单元编码"
                  align="center"
                  min-width="150"
                />
                <ec-table-col
                  field="baseCellName"
                  title="基地单元名称"
                  align="center"
                  min-width="150"
                />
                <ec-table-col
                  field="linkInduProvOrgName"
                  title="省级工业公司"
                  align="center"
                  min-width="150"
                />
                <ec-table-col
                  field="linkInduOrgName"
                  title="对口工业企业"
                  align="center"
                  min-width="150"
                />
                <ec-table-col field="brandName" title="品牌" align="center" min-width="100" />
                <ec-table-col field="leafTypeCode" title="烟叶类型" min-width="100" align="left">
                  <template #default="{ row }">
                    <div>
                      {{ row.leafTypeCode | getNameBy(leafTypeObj) }}
                    </div>
                  </template>
                </ec-table-col>
                <ec-table-col
                  field="leafVarietyName"
                  title="烟叶品种"
                  align="center"
                  min-width="100"
                />
                <ec-table-col field="fieldArea" title="基本烟田面积" min-width="120" align="left">
                  <template #default="{ row }">
                    <div>{{ row.fieldArea | amountFormat(areaUnit) }}</div>
                  </template>
                </ec-table-col>
                <ec-table-col field="plantArea" title="种植面积" min-width="100" align="left">
                  <template #default="{ row }">
                    <div>{{ row.plantArea | amountFormat(areaUnit) }}</div>
                  </template>
                </ec-table-col>
                <ec-table-col field="buyWeight" title="收购量" min-width="100" align="left">
                  <template #default="{ row }">
                    <div>{{ row.buyWeight | amountFormat(weightUnit) }}</div>
                  </template>
                </ec-table-col>
                <ec-table-col
                  field="techSuppOrg"
                  title="技术依托单位"
                  align="center"
                  min-width="150"
                />
              </ec-table>
            </ec-v-item>
          </ec-v-layout>
        </ec-panel-body>
      </ec-panel>
      <clear-edit
        ref="editRef"
        :busiYear="busiYear"
        :replaceFields="replaceFields"
        :businessOrgList="businessOrgList"
        :leafTypeObj="leafTypeObj"
      ></clear-edit>
    </ec-container-body>
  </ec-container>
</template>

<script>
import clearEdit from './clearEdit.vue';
import { SelectPicker } from '@euler-material/ecm-ui';
import { treeFormatList } from '@euler-material/ecm-utils';

export default {
  name: 'Index',
  components: {
    clearEdit,
    SelectPicker,
  },
  data() {
    return {
      loading: false,
      // 查询数据
      query: {},
      // 重量单位
      weightUnitList: [],
      // 面积单位
      areaUnitList: [],
      // 当前重量单位
      weightUnit: '500000',
      // 当前面积单位
      areaUnit: '10000',
      // 表格数据
      tableData: [],
      // 分页
      pagination: {
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
      // 选中表格数据
      selTableData: [],
      // 烟叶类型列表
      leafTypeList: [],
      // 商业单位
      businessOrgList: [],
      // 工业单位
      industryOrgList: [],
      // 组织字段重置
      replaceFields: {
        pkey: 'parentBizCode',
        title: 'orgName',
        key: 'bizCode',
        children: 'children',
      },
      // 调拨比例
      asPercentTemp: undefined,
    };
  },
  computed: {
    leafTypeObj() {
      return this.leafTypeList.map((item) => {
        return {
          key: item.leafTypeCode,
          title: item.leafTypeName,
        };
      });
    },
    busiYear() {
      return this.$store.getters.getAppConfig?.yearValueMap?.CENTER_YEAR;
    },
    bizCode() {
      return this.$store.getters.getAppConfig?.bizCode;
    },
  },
  async created() {
    // 通过路由获取appCode
    const { appCode } = this.$router.history.current.query;
    this.appCode = appCode || 'CM';
    this.query.busiYear = this.busiYear;

    this.handleGetEnum();
    this.handleQueryLeafTypeList();
    await this.handleQueryBusinessOrgList();
    await this.handleQueryIndustryOrgList();
  },
  methods: {
    // 查询枚举值
    handleGetEnum() {
      const params = {
        enumTypeCodes: ['WEIGHT_UNIT', 'AREA_UNIT'],
      };
      this.$apis.bim.enumType.query(params).then((res) => {
        const enumMap = res?.enumMap || {};
        this.weightUnitList = enumMap.WEIGHT_UNIT || [];
        this.areaUnitList = enumMap.AREA_UNIT || [];
      });
    },
    // 查询烟叶类型
    handleQueryLeafTypeList() {
      this.$apis.bim.leafType.query({}).then((res) => {
        this.leafTypeList = res?.rows || [];
      });
    },
    // 分页变化
    onPageAction(pageNumber, pageSize) {
      if (this.asPercentTemp) {
        this.query.asPercent = this.asPercentTemp / 100;
      }

      const params = {
        pageNo: pageNumber,
        pageSize,
        ...this.query,
      };
      this.loading = true;
      this.$apis.apm.baseClear
        .query(params)
        .then((res) => {
          this.loading = false;
          this.tableData = res?.rows || [];
          this.pagination.total = res?.totalRows;
        })
        .catch(() => {
          this.loading = false;
        });
    },
    // 查询
    onQuery() {
      this.$refs.tableRef.pageAction(1, 10);
    },
    // 重置
    onReset() {
      this.asPercentTemp = undefined;
      this.query = { busiYear: this.busiYear };
      this.onQuery();
    },
    // 查询商业单位
    async handleQueryBusinessOrgList() {
      const params = {
        busiYear: this.busiYear,
        orgTypeEk: 'TOBACCO',
        orgLevelEk: '2',
        parentBizCode: this.bizCode,
      };
      const res = await this.$apis.bim.org.queryBy(params);
      this.businessOrgList = res.rows || [];
    },
    // 更新商业单位
    onFetchBusinessOrgList() {
      return new Promise((resolve) => {
        const data = treeFormatList(this.businessOrgList, this.replaceFields);
        resolve(data);
      });
    },
    // 查询工业单位
    async handleQueryIndustryOrgList() {
      const params = {
        busiYear: this.busiYear,
        orgTypeEk: 'INDUSTRY',
        orgLevelEk: '2',
        parentBizCode: this.bizCode,
      };
      const res = await this.$apis.bim.org.queryBy(params);
      this.industryOrgList = res.rows || [];
    },
    // 更新工业单位
    onFetchIndustryOrgList() {
      return new Promise((resolve) => {
        const data = treeFormatList(this.industryOrgList, this.replaceFields);
        resolve(data);
      });
    },
    // table选中对应项
    onSelTableData({ records }) {
      this.selTableData = records;
    },
    // table选中所有项
    onSelAllTableData({ records }) {
      this.selTableData = records;
    },
    // 新增/编辑
    handleUpdate() {
      this.$refs.editRef.show();
    },
    // 清理
    onClear() {
      if (!this.selTableData.length) {
        this.$message.error('请选择一条数据再清理');
        return;
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认清理选中的数据，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          const params = {
            busiYear: this.busiYear,
            bmUnitIds: this.selTableData.map((item) => item.bmUnitId),
          };
          this.loading = true;
          this.$apis.apm.baseClear.clear(params).then(() => {
            this.loading = false;
            this.$message.success('清理成功');
            this.onQuery();
          });
        },
      });
    },
    // 计算
    onCalculate() {
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认要计算上一年调拨比例，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          const params = {
            busiYear: this.busiYear,
          };
          this.loading = true;
          this.$apis.apm.baseClear.calculate(params).then(() => {
            this.loading = false;
            this.$message.error('计算成功');
            this.onQuery();
          });
        },
      });
    },
  },
};
</script>
<style lang="less" scoped>
.horizontal-row {
  /deep/.ec-query-item__control {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    & > span {
      width: auto;
      white-space: nowrap;
    }
    input {
      margin: 0 12px;
    }
    .ant-input-number {
      margin: 0 12px;
    }
  }
}
.unit-box {
  display: flex;
  align-items: center;
  .select {
    margin-left: 20px;
    /deep/ .ant-select {
      width: 100px;
    }
  }
}
</style>
