<template>
  <div>
    <ec-drawer
      title="已清理单位"
      :visible="visible"
      size="large"
      @close="onCancel"
      :is-confirm="true"
    >
      <ec-container height="100%">
        <ec-container-body>
          <ec-panel :auto="false">
            <ec-panel-toolbar>
              <ec-query-panel :defaultFold="true" @query="onQuery" @reset="onReset">
                <ec-query-item label="清理年度">
                  <a-input-number v-model="query.clearYear" :min="2000" :max="2099" />
                </ec-query-item>
                <ec-query-item label="商业单位">
                  <select-picker
                    title="选择商业单位"
                    :lazy="false"
                    :search="false"
                    :fetchData="onFetchBusinessOrgList"
                    v-model="query.businesOrgCode"
                  />
                </ec-query-item>
                <ec-query-item label="办理依据">
                  <a-input v-model.trim="query.remark" placeholder="" />
                </ec-query-item>
              </ec-query-panel>
              <ec-divider />
            </ec-panel-toolbar>
            <ec-panel-body>
              <ec-v-layout :auto="false">
                <!-- 表格工具栏 -->
                <ec-v-item height="auto">
                  <ec-panel-toolbar>
                    <ec-panel-item-left>
                      <a-button type="primary" @click="onExport">导出</a-button>
                    </ec-panel-item-left>
                  </ec-panel-toolbar>
                </ec-v-item>
                <!-- 列表 -->
                <ec-v-item>
                  <ec-table
                    ref="tableRef"
                    :loading="loading"
                    show-overflow
                    height="100%"
                    :auto="false"
                    @page-action="onPageAction"
                    :pagination="pagination"
                    :data="tableData"
                  >
                    <ec-table-col type="seq" title="序号" width="50" align="center" />
                    <ec-table-col
                      field="clearYear"
                      title="清理年度"
                      align="center"
                      min-width="100"
                    />
                    <ec-table-col
                      field="clearTime"
                      title="清理日期"
                      align="center"
                      min-width="100"
                    />
                    <ec-table-col
                      field="provOrgName"
                      title="省公司"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="cityOrgName"
                      title="市公司"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="provDivisionName"
                      title="省"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="cityDivisionName"
                      title="地(州、市)"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="counDivisionName"
                      title="县(区、市)"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="baseCellCode"
                      title="基地单元编码"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="baseCellName"
                      title="基地单元名称"
                      align="center"
                      min-width="150"
                    />
                    <ec-table-col
                      field="leafTypeCode"
                      title="烟叶类型"
                      min-width="100"
                      align="left"
                    >
                      <template #default="{ row }">
                        <div>
                          {{ row.leafTypeCode | getNameBy(leafTypeObj) }}
                        </div>
                      </template>
                    </ec-table-col>
                    <ec-table-col
                      field="leatVarietyName"
                      title="烟叶品种"
                      align="center"
                      min-width="100"
                    />
                    <ec-table-col
                      field="fieldArea"
                      title="基本烟田面积(万亩)"
                      min-width="150"
                      align="left"
                    >
                      <template #default="{ row }">
                        <div>{{ row.fieldArea | amountFormat(areaUnit) }}</div>
                      </template>
                    </ec-table-col>
                    <ec-table-col
                      field="plantArea"
                      title="种植面积(万亩)"
                      min-width="150"
                      align="left"
                    >
                      <template #default="{ row }">
                        <div>{{ row.plantArea | amountFormat(areaUnit) }}</div>
                      </template>
                    </ec-table-col>
                    <ec-table-col
                      field="buyWeight"
                      title="收购量(万担)"
                      min-width="150"
                      align="left"
                    >
                      <template #default="{ row }">
                        <div>{{ row.buyWeight | amountFormat(weightUnit) }}</div>
                      </template>
                    </ec-table-col>
                    <ec-table-col field="remark" title="办理依据" align="center" min-width="150" />
                  </ec-table>
                </ec-v-item>
              </ec-v-layout>
            </ec-panel-body>
          </ec-panel>
        </ec-container-body>
        <ec-container-footer>
          <a-button @click="onCancel">关闭</a-button>
        </ec-container-footer>
      </ec-container>
    </ec-drawer>
  </div>
</template>
<script>
import { SelectPicker } from '@euler-material/ecm-ui';
import { treeFormatList } from '@euler-material/ecm-utils';

export default {
  name: 'BusinessOrgManageEdit',
  components: {
    SelectPicker,
  },
  props: {
    businessOrgList: {
      type: Array,
      default: () => [],
    },
    busiYear: {
      type: Number,
      default: 0,
    },
    replaceFields: {
      type: Object,
      default: () => {},
    },
    leafTypeObj: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      loading: false,
      visible: false,
      query: {},
      tableData: [],
      pagination: {
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
      businesOrgCodeTemp: undefined,
      // 当前重量单位
      weightUnit: '500000',
      // 当前面积单位
      areaUnit: '10000',
    };
  },
  methods: {
    show() {
      this.visible = true;
      this.query = {
        clearYear: this.busiYear,
        busiYear: this.busiYear,
      };

      this.onQuery();
    },
    // 更新商业单位
    onFetchBusinessOrgList() {
      return new Promise((resolve) => {
        const data = treeFormatList(this.businessOrgList, this.replaceFields);
        resolve(data);
      });
    },
    // 分页变化
    onPageAction(pageNumber, pageSize) {
      if (!this.query.clearYear && !this.query.busiYear) return;
      const params = {
        pageNo: pageNumber,
        pageSize,
        ...this.query,
      };
      this.loading = true;
      this.$apis.apm.baseClear
        .clearQuery(params)
        .then((res) => {
          this.loading = false;
          this.tableData = res?.rows || [];
          this.pagination.total = res?.totalRows;
        })
        .catch(() => {
          this.loading = false;
        });
    },
    // 查询
    onQuery() {
      this.$refs?.tableRef?.pageAction(1, 10);
    },
    // 重置
    onReset() {
      this.query = {
        clearYear: this.busiYear,
        busiYear: this.busiYear,
      };
      this.onQuery();
    },
    // 取消
    onCancel() {
      this.visible = false;
      this.onReset();
    },
    // 导出
    onExport() {},
  },
};
</script>
